@media only screen and (min-width: 1080px){

  *{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
  }


  body{
    height: 100vh;
    width: 100vw;
  }

  html{
    width: 100vw;
    height: 100vh;
  }
  
  a,li{
    text-decoration: none;
    list-style-type: none;
    color:aliceblue;
    font-size: 2.5rem;
    font-weight: 100;
  }
  
  a{
    color:aliceblue;
  }
  
  
  .laptop{
  position: fixed;
  
  
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  
  }
.home{
  width: 100vw;
  height: 100vh;
}
  
  ul{
    display: flex;
    width: 95vw;
    height: 17vh;
  justify-content: space-around;
 
 
  }
  
  body{
    height: 120vh;
  }

  .home{
    width: 100vw;
    height: 100vh;
  }
  
  img{
    width: 30vw;
  }

  .img{
    width: 100vw;
    height: 100vh;
  }
  
  .IMG{
    height: 15vh;
    width: 25vh;
  }

  .logo{
    width: 100vw;
    height: 100vh;
  }

}


@media only screen and (max-width: 600px){
  *{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
  }
  
  a,li{
    text-decoration: none;
    list-style-type: none;
    color:aliceblue;
    font-size: 2.5rem;
    font-weight: 100;
  }

  .laptop{
    display: none;
  }

  .logo{
    width: 100vw;
    height: 100vh;
    
  }

}